/* home_contact_form */
.home_contact_form{
	margin-top:30px;
}
.home_contact_form__row {
	position: relative;
}
.home_contact_form__row input,.home_contact_form__row textarea{background:#f8f8f8;resize:none;font-family:inherit;font-size:18px;color:#444;padding:0 10px;border:1px solid #ffa834}
.home_contact_form__row input{height:45px}
