.home_contact_form__row:not(:last-child){margin-bottom:20px}
.home_contact_form__holder form.success .message_success{opacity:1}

.home_contact_form__row.required::before {
    content: '*';
    position: absolute;
    top: 3px;
    right: 6px;
    color: #e9a734;
    font-size: 24px;
    font-size: 1.5rem;
}

.home_contact_form__row .btn_style_1 {
	display: block;
	width: 250px;
	margin: 0 auto;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
    border-color: red
}

body input:required:valid, body textarea:required:valid {
    border-color: green
}


