header { position: fixed; top: 0; left: 0; right: 0; z-index: 200; }

header .wrapper { padding: 15px 30px; -moz-transition: 0.3s; -o-transition: 0.3s; -webkit-transition: 0.3s; transition: 0.3s; border-bottom: 1px solid transparent; background: rgba(255, 255, 255, 1); -webkit-box-shadow: 0px -3px 5px 4px rgba(130, 130, 130, 0.41); -moz-box-shadow: 0px -3px 5px 4px rgba(130, 130, 130, 0.41); -ms-box-shadow: 0px -3px 5px 4px rgba(130, 130, 130, 0.41); box-shadow: 0px -3px 5px 4px rgba(130, 130, 130, 0.41); }

header .wrapper:before { content: ""; display: table; }

header .wrapper:after { content: ""; display: table; clear: both; }

header.float .wrapper { background: rgba(255, 255, 255, 1); padding: 5px 30px; }

header.float .header__logo { /*width: 100px;*/ }

.header__logo__wrap { }

.header__logo { display: block; width: 116px; height: 40px; background-image: url("../img/common/team-logo.png"); background-size: 100%; background-position: center; background-repeat: no-repeat; -moz-transition: 0.3s; -o-transition: 0.3s; -webkit-transition: 0.3s; transition: 0.3s; }

.header__right__wrap {margin-left: auto }

.header__right__wrap .select_block { width: 80px; display: inline-block; vertical-align: middle; border: 2px solid #FF9F00; margin-left: 50px; height: 26px; -webkit-border-radius: 5px; border-radius: 5px; background-clip: padding-box; }

.header__right__wrap .select_block select { font-size: 16px; font-family: "RubikMedium"; }

