.header__nav a:after { content: ""; position: absolute; display: block; z-index: 100; width: 100%; opacity: 0; height: 2px; -moz-transition: 0.3s; -o-transition: 0.3s; -webkit-transition: 0.3s; transition: 0.3s; background: #000; top: 16px; }

