/* header__members_actions */
.header__members_actions {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-left: 35px;
}
.header__members__preview {
    line-height: 1;
    font-size: 24px;
    color: #0088BB;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.header__members__preview .arrow {
    width: 10px;
    height: 10px;
    display: inline-block;
    vertical-align: middle;
    border-bottom: 3px solid #444;
    border-right: 3px solid #444;
    margin-left: 5px;
    border-color: #ff9404;
    margin-top: -7px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
}
.header__members__dropdown {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 100;
    width: 160px;
    margin-top: 12px;
    padding: 5px 0;
    list-style: none;
    background-color: #fff;
    border: 1px solid rgba(27,31,35,0.15);
    border-radius: 4px;
    box-shadow: 0 3px 12px rgba(27,31,35,0.15);
    display: none;
}

.header__members__dropdown.active {
    display: block;
}

.header__members__dropdown__row {

}



.header__members__dropdown::before, .header__members__dropdown::after {
    position: absolute;
    display: inline-block;
    content: "";
}

.header__members__dropdown::before {
    border: 8px solid transparent;
    border-bottom-color: rgba(27,31,35,0.15);
}

.header__members__dropdown::before, .header__members__dropdown::after {
    position: absolute;
    display: inline-block;
    content: "";
}

.header__members__dropdown::before {
    top: -16px;
    right: 9px;
    left: auto;
}

.header__members__dropdown::after {
    border: 7px solid transparent;
    border-bottom-color: #fff;
}

.header__members__dropdown::after {
    top: -14px;
    right: 10px;
    left: auto;
}

