/* header__nav_btn */
.header__nav_btn { position: relative; display: none; width: 36px; height: 36px; cursor: pointer; user-select: none; margin-top: 2px; float: right; overflow: hidden; border: 2px solid #FF9F00; -webkit-border-radius: 2px; border-radius: 2px; background-clip: padding-box; }

.header__nav_btn span { -moz-transition: 0.2s; -o-transition: 0.2s; -webkit-transition: 0.2s; transition: 0.2s; height: 4px; width: 28px; background-color: #FF9F00; position: absolute; left: 50%; margin-left: -14px; top: 50%; margin-top: -2px; }

.header__nav_btn span:first-of-type { top: 6px; margin-top: 0; }

.header__nav_btn span:last-of-type { top: auto; bottom: 6px; }

.header__nav_btn.open_nav span:first-of-type { -moz-transform: rotate(45deg); -o-transform: rotate(45deg); -ms-transform: rotate(45deg); -webkit-transform: rotate(45deg); transform: rotate(45deg); top: 50%; margin-top: -2px; }

.header__nav_btn.open_nav span:nth-child(2) { left: 200%; }

.header__nav_btn.open_nav span:last-of-type { -moz-transform: rotate(-45deg); -o-transform: rotate(-45deg); -ms-transform: rotate(-45deg); -webkit-transform: rotate(-45deg); transform: rotate(-45deg); top: auto; bottom: 50%; margin-bottom: -2px; }

