.go_top_btn { position: fixed; bottom: -20px; right: 20px; font-size: 16px; text-align: center; -moz-transition: 0.3s; -o-transition: 0.3s; -webkit-transition: 0.3s; transition: 0.3s; width: 21px; opacity: 0; cursor: pointer; height: 36px; line-height: 36px; background: #ff9f00; z-index: 100; }

.go_top_btn.show { opacity: 1; bottom: 20px; }

.go_top_btn:before, .go_top_btn:after { content: " "; width: 0; height: 0; top: 0; position: absolute; }

.go_top_btn:before { border-right: 10px solid; border-top: 18px solid transparent; border-bottom: 18px solid transparent; left: -10px; border-right-color: #ff9f00 !important; }

.go_top_btn:after { border-left: 10px solid; border-top: 18px solid transparent; border-bottom: 18px solid transparent; right: -10px; border-left-color: #ff9f00 !important; }

.go_top_btn i {
    color: #fff;
}

